.About-footer-horizontal {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  position: absolute;
  bottom: 5vh;
  width: 60%;
}
.About-footer-vertical {
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: center;
  margin-left: 1vw;
  align-items: baseline;
  height: 85vh;
}
.About-image {
  width: 24px;
}
.About-image:hover {
  fill: #0070ff !important;
}
